.table{
    margin-top: 1rem;
    overflow-y: scroll;
    width: 21.5rem;
    height: 18rem;
    color: rgb(39, 39, 39);
}
table{
    width: 20.8rem;
    font-family: 'Montserrat';
}
.table tr{
    display: flex;
    justify-content: space-between;
}

.table td{
    padding: 0.3rem;
}

table:nth-of-type(odd){
    background-color: rgba(128, 128, 128, 0.205);
}

@media only screen and (min-width: 320px) and (max-width: 375px){
    .table{
        margin-top: 1.5rem;
        overflow-y: scroll;
        width: 13.5rem;
        height: 18rem;
        color: rgb(39, 39, 39);
    }  
    table{
        width: 12.8rem;
    }
}

@media only screen and (min-width: 390px) and (max-width: 480px){
    .table{
        margin-top: 1.5rem;
        overflow-y: scroll;
        width: 14.5rem;
        height: 18rem;
        color: rgb(39, 39, 39);
    }  
    table{
        width: 13.8rem;
    }
}

@media only screen and (min-width: 768px){
    .table{
        margin-top: 1.5rem;
        overflow-y: scroll;
        width: 38rem;
        height: 18rem;
        color: rgb(39, 39, 39);
    } 
    table{
        width: 37.3rem;
    }
}

@media only screen and (min-width: 1024px){
   .table{
    margin-top: 1.5rem;
    overflow-y: scroll;
    width: 15rem;
    height: 15rem;
    color: rgb(39, 39, 39);
   } 
   table{
    width: 14.3rem;
}
}

@media only screen and (min-width: 1366px){
    .table{
        margin-top: 1.5rem;
        overflow-y: scroll;
        width: 17rem;
        height: 18rem;
    }
    table{
        width: 16.4rem;
    }
}